@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  user-select: none;
}
p {
  text-shadow: 0 1px 2px rgba(0,0,0,0.7);
}
.head {
  width: 100%;
  min-width: 370px;
  padding: 10px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, rgb(4,216,230), rgb(16,80,144));
  position: relative;
  overflow: hidden;
}
.card-form{
  max-width: 500px;
  min-width: 300px;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.card-form input{
  width: 70%;
  border-radius: 5px;
  border: none;
  outline: none;
  padding: 5px 10px;
  background-color: transparent;
  box-shadow: 0 3px rgba(0,0,0,0.5);
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  position: relative;
  border: 1px solid #fff;
}
.card-form input::placeholder{
  color: #fff;
  font-size: 18px;
  font-weight: 500;
}
.card-form button{
  width: 25%;
  padding: 5px 10px;
  outline: none;
  border-radius: 5px;
  border: 1px solid rgba(255,255,255,0.6);
  background-color: rgba(255,255,255,0.5);
  box-shadow: 0 3px rgba(0,0,0,0.5);
  text-shadow: 0 0 2px rgba(0,0,0,1);
  font-size: 20px;
  font-weight: 700;
  transition: 0.5s;
}
.card-form button:active{
  transform: scale(0.8);
}
.info-card{
  opacity: 0;
  max-width: 500px;
  min-width: 300px;
  width: 90%;
  height: 400px;
  border-radius: 10px;
  background: rgba(255,255,255,0.2);
  box-shadow: -1px 2px 7px rgba(0,0,0,0.5);
  backdrop-filter: blur(9px);
  overflow: hidden;
  position: relative;
}
.info-card::before{
  content: '';
  width: 200%;
  height: 200%;
  border-radius: 45%;
  position: absolute;
  top: 20%;
  left: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.01), rgba(255, 255, 255, 0.2), rgba(255,255,255,0.8));
}
.qator{
  width: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.qator:first-child{
  width: 90%;
  padding-top: 10px;
}
.qator:first-child:before{
  content: '';
  width: 90%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  box-shadow: 0 0 3px #fff;
}
.city-name{
  display: flex;
  align-items: center;
}
.city-name h2{
  margin-left: 5px;
  font-weight: 700;
} 
.qator:nth-of-type(2){
  width: 100%;
  padding: 7px 5%;
  background-color: rgb(20, 94, 168);
  box-shadow: 0 0 3px rgba(20, 94, 168, 0.8);
}
.qator:nth-of-type(3){
  height: 200px;
  padding: 25px 0;
  display: flex;
  justify-content: space-between;
}
.column{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: stretch;
}
.weather-img{
  width: 30%;
  height: 100%;
  align-items: start;
}
.weather-wiew{
  margin-top: 18px;
}
.weather-temp{
  width: 45%;
  height: 100%;
  align-items: center;
}
.weather-temp h1 {
  font-size: 90px;
}
.weather-temp h1 sup {
  font-size: 50px;
}
.weather-min-max-temp{
  width: 25%;
  height: 100%;
}
.weather-min-max-temp div{
  margin-top: auto;
}
.minMax{
  font-size: 18px;
}
.weather-min-max-temp div p{
  font-size: 13px;
  line-height: 10px;
}
.weather-min-max-temp:before{
  content: '';
  width: 90%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  box-shadow: 0 0 3px #fff;
}
.qator:nth-of-type(4){
  padding: 10px 0;
}
.box{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px;
  border: 1px solid #fff;
  border-radius: 5px;
  background-color: rgba(150, 150, 150, 0.4);
}
.box img{
  width: 50px;
}
.error{
  opacity: 0;
  position: absolute!important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.arrow{
  width: 18px;
}
.arrowUp{
  transform: rotate(180deg);
}